"use client";

import { useState, useEffect } from 'react';
import { VerseCard } from '@/components/verse-card';
import { MobileNavigation } from '@/components/mobile-navigation';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { storage } from '@/lib/storage';
import { FavoriteVerse } from '@/lib/types';

export default function FavoritesPage() {
  const [favorites, setFavorites] = useState<FavoriteVerse[]>([]);
  const [filteredFavorites, setFilteredFavorites] = useState<FavoriteVerse[]>([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    loadFavorites();
  }, []);

  useEffect(() => {
    filterFavorites();
  }, [favorites, searchQuery]);

  const loadFavorites = () => {
    try {
      const savedFavorites = storage.getFavorites();
      setFavorites(savedFavorites);
    } catch (error) {
      console.error('Failed to load favorites:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const filterFavorites = () => {
    if (!searchQuery.trim()) {
      setFilteredFavorites(favorites);
      return;
    }

    const query = searchQuery.toLowerCase();
    const filtered = favorites.filter(favorite =>
      favorite.text.toLowerCase().includes(query) ||
      favorite.reference.toLowerCase().includes(query) ||
      (favorite.topic && favorite.topic.toLowerCase().includes(query)) ||
      (favorite.notes && favorite.notes.toLowerCase().includes(query))
    );
    setFilteredFavorites(filtered);
  };

  const handleFavoriteRemoved = (removedFavorite: FavoriteVerse) => {
    setFavorites(prev => prev.filter(fav => fav.id !== removedFavorite.id));
  };

  const clearAllFavorites = () => {
    if (window.confirm('Are you sure you want to remove all saved verses? This action cannot be undone.')) {
      favorites.forEach(favorite => storage.removeFavorite(favorite.id));
      setFavorites([]);
    }
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center pb-20">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-amber-600 mx-auto mb-4"></div>
          <p className="text-slate-600 dark:text-slate-400">Loading your favorites...</p>
        </div>
        <MobileNavigation />
      </div>
    );
  }

  return (
    <div className="min-h-screen pb-20">
      {/* Header */}
      <header className="bg-white/80 dark:bg-slate-900/80 backdrop-blur-lg border-b border-amber-200 dark:border-slate-700 sticky top-0 z-40">
        <div className="max-w-md mx-auto px-4 py-4">
          <div className="text-center">
            <h1 className="text-2xl font-bold text-amber-800 dark:text-amber-200 mb-1">
              Favorites
            </h1>
            <p className="text-sm text-slate-600 dark:text-slate-400">
              {favorites.length} saved verse{favorites.length !== 1 ? 's' : ''}
            </p>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-md mx-auto px-4 py-6">
        {favorites.length > 0 ? (
          <>
            {/* Search Bar */}
            <div className="mb-6">
              <div className="relative">
                <svg className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-slate-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                </svg>
                <Input
                  type="text"
                  placeholder="Search your saved verses..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="pl-10 bg-white/70 dark:bg-slate-800/70 backdrop-blur-sm border-amber-200 dark:border-slate-600"
                />
              </div>
            </div>

            {/* Clear All Button */}
            {favorites.length > 0 && (
              <div className="mb-6 text-center">
                <Button
                  variant="outline"
                  size="sm"
                  onClick={clearAllFavorites}
                  className="text-red-600 border-red-200 hover:bg-red-50 dark:text-red-400 dark:border-red-800 dark:hover:bg-red-900/20"
                >
                  Clear All Favorites
                </Button>
              </div>
            )}

            {/* Favorites List */}
            {filteredFavorites.length > 0 ? (
              <div className="space-y-6">
                {filteredFavorites.map((favorite) => (
                  <div key={favorite.id} className="relative">
                    <VerseCard
                      verse={favorite}
                      showReflection={true}
                      showFavoriteButton={true}
                      isFavorite={true}
                      onFavoriteChange={() => handleFavoriteRemoved(favorite)}
                    />
                    {/* Saved Date */}
                    <div className="mt-2 text-xs text-slate-500 dark:text-slate-400 text-center">
                      Saved on {new Date(favorite.savedAt).toLocaleDateString('en-US', {
                        year: 'numeric',
                        month: 'short',
                        day: 'numeric'
                      })}
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              /* No Search Results */
              <div className="text-center py-12">
                <div className="text-slate-400 mb-4">
                  <svg className="w-16 h-16 mx-auto" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                  </svg>
                </div>
                <h3 className="text-lg font-medium text-slate-600 dark:text-slate-400 mb-2">
                  No verses found
                </h3>
                <p className="text-sm text-slate-500 dark:text-slate-500 mb-4">
                  Try adjusting your search terms
                </p>
                <Button
                  variant="outline"
                  onClick={() => setSearchQuery('')}
                  className="text-amber-600 border-amber-200 hover:bg-amber-50 dark:text-amber-400 dark:border-amber-800 dark:hover:bg-amber-900/20"
                >
                  Clear Search
                </Button>
              </div>
            )}
          </>
        ) : (
          /* Empty State */
          <div className="text-center py-12">
            <div className="bg-gradient-to-br from-amber-100 to-blue-100 dark:from-amber-900/20 dark:to-blue-900/20 rounded-full w-24 h-24 flex items-center justify-center mx-auto mb-6">
              <svg className="w-12 h-12 text-amber-600 dark:text-amber-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
              </svg>
            </div>
            <h2 className="text-xl font-semibold text-slate-800 dark:text-slate-200 mb-2">
              No Saved Verses Yet
            </h2>
            <p className="text-slate-600 dark:text-slate-400 text-sm mb-6">
              Start building your collection of meaningful verses by saving them from the Home or Explore pages.
            </p>
            <div className="space-y-3">
              <a
                href="/"
                className="block w-full bg-amber-500 hover:bg-amber-600 text-white font-medium py-3 px-6 rounded-lg transition-colors duration-200"
              >
                View Today's Verse
              </a>
              <a
                href="/explore"
                className="block w-full bg-white dark:bg-slate-800 border border-amber-200 dark:border-slate-600 text-amber-600 dark:text-amber-400 font-medium py-3 px-6 rounded-lg hover:bg-amber-50 dark:hover:bg-slate-700 transition-colors duration-200"
              >
                Explore Random Verses
              </a>
            </div>
          </div>
        )}
      </main>

      <MobileNavigation />
    </div>
  );
}
